import {
	uniTag
} from "@dcloudio/uni-ui"
import rateItem from "@/components/rate-item/rate-item.vue"
import {uniLoadMore} from '@dcloudio/uni-ui'
export default {
	components: {
		uniTag,
		rateItem,
		uniLoadMore
	},
	data() {
		return {
			shopInfo: {},
			currentFilter: 0,
			pager: {
				currentPage: 0,
				pageSize: 15,
				count: 0,
				totalPage: 1
			},
			filterCount: {},
			rateList: [],
			loadMoreStatus: 'more',
			loadMoreText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			rateData: [{"id":27,"orderId":284,"star":2,"comment":"还OK啦","repairItemPhoto":"http://lc-3uxiaoex.cn-n1.lcfile.com/508dcf39f0ed06fdfb9a/IMG_3152.jpeg","createDatetime":"2019-09-04 17:20:043","createBy":38,"avatarUrl":"https://wx.qlogo.cn/mmopen/vi_32/1yzTKJKIfurhDI29RqibEicNOoH0WiaCuKb6jWppVu4uzWovO0d1ICAwuW4rB4zfUxVvGHfNuxXLHu44t3yBkgbicQ/132","username":"Bee","shopId":7},{"id":26,"orderId":283,"star":1,"comment":"差","repairItemPhoto":null,"createDatetime":"2019-09-04 16:53:058","createBy":37,"avatarUrl":"https://wx.qlogo.cn/mmopen/vi_32/Q0j4TwGTfTJOLwsnM32ibwcmgnRhXRzMVlUiaFYkEFQFbYLRic17QA9KMSJJicab42r3cmkEbs7nmm18Y14M8MDGqg/132","username":"吴义增","shopId":7},{"id":25,"orderId":280,"star":5,"comment":"不错","repairItemPhoto":"http://lc-3uxiaoex.cn-n1.lcfile.com/508dcf39f0ed06fdfb9a/IMG_3152.jpeg","createDatetime":"2019-09-04 15:17:059","createBy":39,"avatarUrl":"https://wx.qlogo.cn/mmopen/vi_32/Q0j4TwGTfTJOLwsnM32ibwcmgnRhXRzMVlUiaFYkEFQFbYLRic17QA9KMSJJicab42r3cmkEbs7nmm18Y14M8MDGqg/132","username":"吴义增","shopId":7},{"id":24,"orderId":274,"star":3,"comment":"一般般","repairItemPhoto":null,"createDatetime":"2019-09-04 10:44:035","createBy":37,"avatarUrl":"https://wx.qlogo.cn/mmopen/vi_32/1yzTKJKIfurhDI29RqibEicNOoH0WiaCuKb6jWppVu4uzWovO0d1ICAwuW4rB4zfUxVvGHfNuxXLHu44t3yBkgbicQ/132","username":"Bee","shopId":7},{"id":23,"orderId":277,"star":5,"comment":"环境特别好，师傅人也很好，不错","repairItemPhoto":"http://lc-3uxiaoex.cn-n1.lcfile.com/508dcf39f0ed06fdfb9a/IMG_3152.jpeg","createDatetime":"2019-09-04 10:33:025","createBy":36,"avatarUrl":"https://wx.qlogo.cn/mmopen/vi_32/1yzTKJKIfurhDI29RqibEicNOoH0WiaCuKb6jWppVu4uzWovO0d1ICAwuW4rB4zfUxVvGHfNuxXLHu44t3yBkgbicQ/132","username":"Bee","shopId":7}]
		}
	},
	onLoad(param) {
		let shopId = param.shopId
		this.loadShopDetail(shopId)
		this.loadEvaluation(shopId)
		this.initEvaluationFilterCount(shopId)
	},
	onReachBottom(event) {
		console.log('onReachBottom...')
		if (this.pager.currentPage >= (this.pager.totalPage - 1)) {
			console.log('no data')
			this.loadMoreStatus = 'noMore';
			return false;
		}
		if (this.pager.currentPage < this.pager.totalPage) {
			++this.pager.currentPage;
		}
		this.loadEvaluation()
	},
	methods: {
		loadShopDetail(shopId) {
			let that = this
			that.$api.get({
					url: '/open/shop/' + shopId
				})
				.then((res) => {
					that.shopInfo = res.data.data
				})
				.catch((res) => {
					this.$requestUtils.getErrorMessage('获取店铺信息失败。')
				})
		},
		goToMap(longitude, latitude) {
			console.log(longitude + '------' + latitude)
			uni.navigateTo({
				url: `../shop-location-map/shop-location-map?longitude=${longitude}&latitude=${latitude}`
			})
		},
		makePhoneCall(event) {
			console.log('makePhoneCall')
			var phoneNumber = event.currentTarget.dataset.phone
			uni.makePhoneCall({
				phoneNumber: phoneNumber, //仅为示例
				fail(res) {
					uni.showToast({
						icon: 'none',
						title: "呼叫失败，请稍后再试或联系管理员"
					})
				}
			});
		},
		initEvaluationFilterCount(shopId) {
			let that = this;
			that.$api.get({
					url: `/open/shop/${shopId}/evaluations/count`,
				})
				.then((res) => {
					console.log('res', res)
					that.filterCount = res.data.data;
				})
				.catch((res) => {
					console.log('获取店铺信息失败。',res)
				})
		},
		
		orderClickHandle(event) {
			console.log('orderClickHandle...')
		},
		changeTag(currentTag) {
			if (this.currentFilter == currentTag) {
				return 0;
			}
			this.currentFilter = currentTag;
			this.pager.currentPage = 0;
			this.pager.totalPage = 1;
			this.loadEvaluation()
		},
		
		
		loadEvaluation(shopId) {
			shopId = shopId || this.shopInfo.id;
			let that = this;
			console.log(that.currentFilter);
			that.$api.get({
				url: `/open/shop/${shopId}/evaluations`,
				data: {
					filter: that.currentFilter,
					page: that.pager.currentPage,
					size: that.pager.pageSize,
				}
			})
			.then((res) => {
				if (that.pager.currentPage == 0) {
					that.rateList = res.data.data;
				} else {
					that.rateList = that.rateList.concat(res.data.data);
				}
				that.pager.count = res.data.count;
				if (res.data.count && res.data.count > 0) {
					that.pager.totalPage = Math.ceil(res.data.count / that.pager.pageSize)
					
				} else {
					that.pager.totalPage = 1;
				}
				if (that.pager.totalPage == 1) {
					that.loadMoreStatus = 'noMore';
				}
			})
			.catch((res) => {
				console.log('获取店铺信息失败。',res)
				this.pager.count = 0;
				this.pager.totalPage = 1;
			})
		}
		
	}
}
